/* App.css */
:root {
  --fnt: normal normal normal 22px/1.4em avenir-lt-w01_85-heavy1475544,avenir-lt-w05_85-heavy,sans-serif;
  --trans: color 0.4s ease 0s;
  --lightfnt: normal normal normal 16px/1.4em wfont_fad1d1_2732e95b49874e1aacd4b1c7d7020c42,wf_2732e95b49874e1aacd4b1c7d,orig_inter_regular;
}
body{
  margin: 0;
}
.body {
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 300;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.innerbody{
  padding-left: 15%;
  padding-right: 15%;
}

.header {
  padding-left: 10%;
  padding-top: 2.5%;
  padding-right: 10%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header a{
  text-decoration: none;
}

.header h1 {
  margin: 0;
  font: var(--fnt);
  transition: color .4s ease;
  color: #000000;
}

.header h1:hover{
  color: #777;
}

.black-text{
    color: #000000;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    line-height: 1.4em;
    letter-spacing: 0px;
}

.gradient-text {
  background: linear-gradient(45deg, #fc5c7d, #6a82fb); /* Gradient colors */
  -webkit-background-clip: text; /* Clip the text to the background */
  color: transparent; /* Make the text transparent */
  display: inline-block; /* Ensure the gradient is applied to each letter individually */
}

.light-text{
  font-size: 16px;
  line-height: 1.4em;
  color: #787878;
  font: var(--lightfnt);
}

.about{
  display: flex;
  height: auto;
  margin-top: 10%!important;
}


.company{
  color: #000;
  text-decoration: none;
  font-size: 16px;
  line-height: 1.4em;
  font-family: 'Inter', sans-serif;
}

.header p {
  margin: 0;
}

.social-links {
  display: flex;
  justify-content: center;
  margin-top: 20px; /* Add some space between header and social links */
}

.social-links a {
  font-family: 'Inter', sans-serif;
  margin: 0 10px;
  color: #777;
  text-decoration: none;
  font-size: 1.3rem;
  transition: color 0.3s ease;
}

.social-links a:hover {
  color: #000000; /* Slightly darken the color on hover */
}

.education-item{
  padding: 5%;
  display: grid;
  height: auto;
  grid-template-rows: repeat(3, min-content) 1fr;
  grid-template-columns: 100%;
}

.flex{
  display: flex;
  height: auto;
  width: 100%;
  flex-wrap: wrap;
    justify-content: center;
}

/* Additional styles for education logos */
.education-item img, 
.project img{
  width: 348px; /* Adjust the width for education logos */
  height: 236px; /* Adjust the height for education logos */
  object-fit:contain;
  object-position: 50% 50%;
  background-color:#f4f4f4
}

.education-item div {
  flex-grow: 1;
}

.pj{
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
}
.project{
  padding: 5%;
}

.pj a{
  text-decoration: none!important;
}

.section {
  margin: 20px;
  padding: 20px;
  background-color: white;
}

.section h2 {
  font-size: 1.5rem; /* Adjust the font size for section headings */
  color: #111; /* Dark color for section headings */
  font-family: 'Inter', sans-serif;
}

.contact{
  background-color: #1e1e1e;
  margin: 0!important;
  width: 100%;
  padding-left: 15%;
  padding-right: 15%;
}
.contact p{
  color: #A0A09F;
}

/* Add more styles as needed */
